// Локальные переменные блока footer
$background_color_footer: $dirty_white_3;
$color_logo: $black;
$color_logo_hover: $turquoise_hover;
$border_color_copyright: $dirty_white_4;
$color_copyright-link: $turquoise_4;
$color_copyright-link_hover: $turquoise_hover;

.footer {
  background-color: $background_color_footer;
}

.footer__inner {
  padding-bottom: 5px;

  @include for_tablet {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding-top: 30px;
    padding-bottom: 33px;
  }
}

.footer__svg {
  fill: currentColor;
}

.footer__logo {
  display: none;
  outline: none;
  color: $color_logo;

  @include for_tablet {
    display: block;
  }

  @include for_desktop {
    &:hover:not(.footer__logo--current),
    &:focus:not(.footer__logo--current) {
      color: $color_logo_hover;
    }

    &:active:not(.footer__logo--current) {
      opacity: $active_opacity;
    }
  }
}

.footer__socials {
  @include for_tablet {
    margin-left: auto;
    margin-right: 82px;
  }

  @include for_desktop {
    margin-right: 274px;
  }
}

.footer__copyright {
  @include font_character($font_size_footer_copyright,
                          $line_height_footer_copyright,
                          $font_weight_footer_copyright,
                          $color_footer_copyright);
  @include link_default_remove;

  min-height: 74px;
  margin: 0;
  margin-top: -4px;
  border-top: 1px solid $border_color_copyright;
  padding-left: 23px;
  padding-right: 23px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  outline: none;

  .footer__copyright-logo {
    margin-top: 6px;
    color: $turquoise_2;
    fill: currentColor;
  }

  @include for_tablet {
    justify-content: space-between;
    padding-left: 0;
    padding-right: 0;
    min-width: 183px;
    border-top: none;
    padding-top: 6px;
  }

  @include for_desktop {
    &:hover,
    &:focus {
      .footer__copyright-logo {
        color: $color_copyright-link_hover;
      }
    }

    &:active {
      opacity: $active_opacity;
    }
  }
}
