// Локальные переменные для блока highlight
$background_color_image-wrapper: $dirty_white_3;

.highlight {
  background-size: 320px 10px;
  background-image: url("../img/bg-zigzag-line.svg");
  background-repeat: repeat-x;
  background-position: 9px 100%;

  @include for_tablet {
    background-size: 770px 24px;
    background-position: 22px 100%;
  }

  @include for_desktop {
    background: none;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      width: 1050px;
      height: 23px;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      background-image: url("../img/bg-zigzag-line.svg");
      background-repeat: repeat-x;
      background-size: 1050px 23px;
      background-position: 26px 100%;
    }
  }
}

.highlight__inner {
  padding-top: 26px;
  padding-bottom: 42px;

  @include for_tablet {
    padding-top: 45px;
    padding-bottom: 69px;
  }

  @include for_desktop {
    padding-top: 123px;
    padding-bottom: 96px;
  }

  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
}

.highlight__header {
  @include for_tablet {
    display: flex;
    justify-content: space-between;
    padding-bottom: 49px;
    margin-left: -3px;
  }

  @include for_desktop {
    flex-direction: row-reverse;
    justify-content: flex-end;
    padding-bottom: 49px;
  }
}

.highlight__heading {
  @include font_character($font_size_heading,
                          $line_height_heading,
                          $font_weight_heading,
                          $color_heading);
  margin: 0;
  padding-bottom: 6px;

  @include for_tablet {
    @include font_character($font_size_heading_tablet,
                            $line_height_heading_tablet,
                            $font_weight_heading,
                            $color_heading);

    padding-bottom: 0;
  }

  @include for_desktop {
    @include font_character($font_size_heading_desktop,
                            $line_height_heading_desktop,
                            $font_weight_heading,
                            $color_heading);
  }
}

.highlight__naming {
  @include font_character($font_size_highlight_naming,
                          $line_height_highlight_naming,
                          $font_weight_highlight_naming,
                          $color_highlight_naming);
  margin: 0;
  margin-left: -1px;
  text-transform: uppercase;
  padding-bottom: 26px;

  @include for_tablet {
    width: 126px;
    align-self: center;
    text-align: center;
    padding-bottom: 0;
    padding-top: 6px;
    padding-right: 27px;
    padding-left: 27px;
  }

  @include for_desktop {
    width: 175px;
    padding-right: 30px;
    padding-top: 9px;
  }
}

.highlight__content {
  @include for_tablet {
    position: relative;
    padding-right: 377px;
  }

  @include for_desktop {
    padding-left: 175px;
    padding-right: 583px;
  }
}

.highlight__description {
  @include font_character($font_size_highlight_description,
                          $line_height_highlight_description,
                          $font_weight_highlight_description,
                          $color_highlight_description);
  margin: 0;
  padding-bottom: 18px;

  @include for_tablet {
    @include font_character($font_size_highlight_description_tablet,
                            $line_height_highlight_description_tablet,
                            $font_weight_highlight_description,
                            $color_highlight_description);

    padding-bottom: 36px;
  }

  @include for_desktop {
    padding-bottom: 50px;
  }
}

.highlight__table {
  @include font_character($font_size_highlight_table,
                          $line_height_highlight_table,
                          $font_weight_highlight_table,
                          $color_highlight_table);
  width: 100%;
  border-collapse: collapse;

  @include for_tablet {
    padding-bottom: 60px;
  }

  @include for_desktop {
    padding-bottom: 110px;
  }
}

.highlight__row {
  border-bottom: 1px solid $border_color;
}

.highlight__cell {
  padding: 0;
  padding-top: 9px;
  padding-bottom: 10px;

  &:first-child {
    width: 37.69%;

    @include for_tablet {
      width: 33.33%;
    }
  }

  @include for_desktop {
    padding-top: 7px;
    padding-bottom: 12px;
  }
}

.highlight__image-wrapper {
  background-color: $background_color_image-wrapper;
  margin-top: 28px;
  margin-bottom: 24px;

  height: 315px;

  @include for_tablet {
    position: absolute;
    top: 8px;
    right: 1px;
    padding-bottom: 0;
    margin-top: 0;
    margin-bottom: 0;

    height: 404px;
  }

  @include for_desktop {
    z-index: -1;
    top: -158px;
    right: 0;

    height: 636px;
  }
}

.highlight__image {
  @include for_tablet {
    width: 333px;
  }

  @include for_desktop {
    width: 525px;
  }
}

.highlight__price {
  font-weight: bold;
  margin: 0;
  padding-right: 4px;
  margin-bottom: 26px;
  text-align: center;

  @include for_tablet {
    @include font_character($font_size_highlight_price_tablet,
                            $line_height_highlight_price_tablet,
                            $font_weight_highlight_price_tablet,
                            $color_highlight_price_tablet);

    padding-top: 55px;
    padding-bottom: 35px;
    margin: 0;
  }

  @include for_desktop {
    padding-right: 0;
    padding-top: 108px;
    padding-bottom: 34px;
  }
}

.highlight__button {
  margin-right: 1px;
  padding-top: 14px;
  padding-bottom: 14px;

  @include for_tablet {
    margin-left: -2px;
  }

  @include for_desktop {
    margin-left: 0;
    margin-right: 0;
  }
}
