.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  position: relative;

  font-family: $font_family;
  font-size: 17px;
  font-weight: 400;
  line-height: 24px;

  margin: 0;
  padding: 0;

  color: $color_body;
  background-color: $background_color_body;

  @include for_mobile {
    min-width: $breakpoint_mobile;
  }

  @include for_tablet {
    min-width: $breakpoint_tablet;
  }

  @include for_desktop {
    min-width: $breakpoint_desktop;
  }
}
